import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from "@angular/router";
import { Items } from '../items';
import { Customers } from '../customers';
import { Tokens } from '../tokens';
import { Orders } from '../orders';
import { Transactions } from '../transactions';
import { ItemsService } from '../items.service';
import { CustomersService } from '../customers.service';
import { TokensService } from '../tokens.service';
import { OrdersService } from '../orders.service';
import { TransactionsService } from '../transactions.service';
import * as $AB from 'jquery';
import * as cjs from 'chart.js';

@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['./admin.component.css'],
  providers: [
    ItemsService,
    CustomersService,
    TokensService,
    OrdersService,
    TransactionsService
  ]
})
export class AdminComponent implements OnInit {
  items!: Items[];
  item!: Items;
  title!: String;
  description!: String;
  unit_price!: Number;
  category!: String;
  available!: Boolean;

  custs!: Customers[];
  cust!: Customers;
  name!: String;
	mobileno!: String;
	token!: String;
	arrival_time!: Date;
	status!: String;
	order_type!: String;
	transaction_id!: String;

  tokens!: Tokens[];
  tok!: Tokens;
  token_date!: String;
  count!: Number;

  orders!: Orders[];
  order!: Orders;
  order_token!: String;
	customer_id!: String;
	created_at!: Date;
	order_status!: String;
	discount!: Number;
	tax!: Number;
	subtotal!: Number;
	grandtotal!: Number;
	order_item!: String;
	feedback!: Boolean;
  comment!: String;

  trans!: Transactions[];
  tran!: Transactions;
	payment_mode!: String;
	amount!: String;

  constructor(
    private itemsService: ItemsService, 
    private customersService: CustomersService, 
    private tokenService: TokensService, 
    private orderService: OrdersService, 
    private transactionsService: TransactionsService,
    private activRoute: ActivatedRoute
  ) { }

  addItem(){
    const item = {
      title : this.title,
      description : this.description,
      unit_price : Number(this.unit_price),
      category : this.category,
      available : true
    }
    // console.log(item);
    this.itemsService.addItem(item)
      .subscribe( (item: any) => {
        this.items.push(item);
        console.log('item added');
      });
    // window.location.reload();
  }

  ngOnInit(): void {

    this.activRoute.queryParams.subscribe( params => {
      var atkn = window.localStorage.getItem('admintkn');
      if (atkn==null)           
        window.location.href = '/signin';
      else {
        if (atkn != params.p) 
          window.location.href = '/signin';
        else {
          $(window).on('load', function(){
            setTimeout(function(){
              $('.splash').fadeOut();
            }, 500);
          });
        }
      }
    
    });

    this.customersService.getCustomers()
      .subscribe( (custs: any) => {
        this.custs = custs;
        // console.log(custs);
      });

    this.itemsService.getMenu()
      .subscribe( (items: any) => {
        items.sort((a:any, b:any) => a.category.localeCompare(b.category));
        this.items = items;
        setTimeout(()=>{
          this.items.forEach(item => {
            $(document).on('click', '#'+item._id, function(){
              let tr = $(this).parent().parent(); var desc, prc;
              if (tr.hasClass('edit-0')) {
                tr.toggleClass('edit-1 edit-0');
                desc = tr.find('td:nth-child(3)').text();
                tr.find('td:nth-child(3)')
                  .html('<textarea class="form-control" rows="2">'+desc+'</textarea>');
                prc = tr.find('td:nth-child(4)').text();
                tr.find('td:nth-child(4)')
                  .html('<input type="text" class="form-control" value="'+prc+'">');               
                $(this).find('span').text(" confirm?");
                return;
              } else {
                tr.toggleClass('edit-1 edit-0');
                desc = tr.find('td:nth-child(3)').find('textarea').val();
                prc = tr.find('td:nth-child(4)').find('input').val();
                let udtitem = {
                  id : String(item._id),
                  description : desc,
                  unit_price: prc
                };
                updateitem(udtitem);
                tr.find('td:nth-child(3)').html(String(desc));
                tr.find('td:nth-child(4)').html(String(prc));
                $(this).find('span').text("");
              }
            });
          });
        }, 1000);
        // console.log(items);
      });

    const updateitem = ((udtitem: any)=> {
      // console.log(udtitem);
      this.itemsService.updateitem(udtitem)
        .subscribe((res:any)=>{
          // alert("item updated!");
        });
    });

    this.tokenService.getTokens()
      .subscribe( (tokens: any) => {
        this.tokens = tokens;
        // console.log(tokens);
      });

    this.orderService.getallOrders()
      .subscribe( (orders: any) => {
        this.orders = orders;
        // console.log(orders);
      });

    this.transactionsService.getTrans()
      .subscribe( (trans: any) => {
        this.trans = trans;
        // console.log(orders);
      });

    $(document).on('click', '#adminlogout', function(){
      window.localStorage.removeItem('admintkn');
      alert('Successfully Logged out!');
      window.location.href = '/signin';
    });

    $('.tabpane.active').fadeIn();

    $(document).on('click','.navitems a', function(){
      $('.navitems.active').toggleClass('active');
      $(this).parent().toggleClass('active');
      let tabid:String = String($(this).attr('id')?.substr(Number($(this).attr('id')?.indexOf('-'))+1));
      $('.tabpane.active').fadeOut(300, function(){
        $('#'+tabid).fadeIn(300);
        $(this).toggleClass('active');
        $('#'+tabid).toggleClass('active');
      });
    });

    cjs.Chart.register(cjs.ArcElement, cjs.LineElement, cjs.BarElement, cjs.PointElement, cjs.BarController, 
      cjs.BubbleController, cjs.DoughnutController, cjs.LineController, cjs.PieController, cjs.PolarAreaController, 
      cjs.RadarController, cjs.ScatterController, cjs.CategoryScale, cjs.LinearScale, cjs.LogarithmicScale, 
      cjs.RadialLinearScale, cjs.TimeScale, cjs.TimeSeriesScale, cjs.Filler, cjs.Legend, cjs.Title, cjs.Tooltip);
    let weekchartid:cjs.ChartItem = $('#orderperday') as unknown as cjs.ChartItem; 
    var weekchart = new cjs.Chart(weekchartid, {
      type: 'line',
      data: {
        labels: ['Monday','Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday', 'Sunday'],
        datasets: [{
          label: 'prev. week',
          data: [16, 18, 21, 19, 25, 26, 29],
          fill: false,
          borderColor: '#ef476f',
          tension: 0.1
        }]
      },
      options: {
        scales: {
          y: {
            max: 30,
            min: 15,
            ticks: {
              stepSize: 3
            }
          }
        }
      }
    });

    let categchartid:cjs.ChartItem = $('#orderpercateg') as unknown as cjs.ChartItem; 
    var categchart = new cjs.Chart(categchartid, {
      type: 'polarArea',
      data: {
        labels: ['Wraps', 'Biryanis & Rice', 'Chinese', 'Pizzas', 'South Indian'],
        datasets: [{
          label: 'for last 5 days',
          data: [56, 52, 74, 59, 72],
          backgroundColor: ['#fb5607', '#4ecdc4', '#00B050', '#ff6b6b', '#ffe66d']
        }]
      },
      options: {
        elements: {
        line: {
          borderWidth: 3
        }
        }
      },
    });

  }

}
